            .header {
                width: 100%;
                height: 5rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                background-color: #77002e;
                padding: 0 10%;
            }
            
            .logo {
                font-size: 2rem;
                color: white;
                font-weight: bold;
            }
            
            .logo span {
                font-size: 2rem;
                color: yellow;
                font-weight: bold;
            }
            
            .header ul {
                list-style: none;
                margin: 0;
                padding: 0;
                display: flex;
                align-items: baseline;
            }
            
            .header li {
                margin-left: 3rem;
            }
            
            .header a {
                text-decoration: none;
                font-size: 1.5rem;
                color: white;
            }
            
            .header a:hover,
            .header a:active,
            .header a.active {
                color: yellow;
            }
            /* //search bar */
            
            .search-container {
                background-color: whitesmoke;
                height: 100px;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                justify-content: center;
            }
            
            .search-element {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: stretch;
                -ms-flex-align: stretch;
                align-items: stretch;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                justify-content: center;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -ms-flex-direction: column;
                flex-direction: column;
                position: relative;
            }
            
            .search-element .form-control {
                padding: 1rem 2rem;
                font-size: 1.4rem;
                border: 1px solid #77002e;
                border-top-left-radius: 3px;
                border-bottom-left-radius: 3px;
                outline: none;
                color: var(--light-dark-color);
                width: 350px;
            }
            
            .badge {
                background-color: #cc2062;
                color: white;
                border-radius: 12px;
                padding: 0 1rem;
                margin-left: 0.5rem;
            }
            
            @media(max-width: 450px) {
                header .logo {
                    font-size: 1.9rem;
                }
                span {
                    font-size: 1.9rem;
                }
                nav {
                    display: none;
                }
                .search-element .form-control {
                    width: 90%;
                    margin: 0 auto;
                    padding: 0.5rem 1rem;
                }
            }
            
            @media(min-width: 800px) {
                header .logo {
                    font-size: 1.9rem;
                }
                span {
                    font-size: 1.9rem;
                }
                nav {
                    font-size: 19px;
                }
                .search-element {
                    -webkit-box-orient: horizontal;
                    -webkit-box-direction: normal;
                    -ms-flex-direction: row;
                    flex-direction: row;
                }
            }